<template>
    <div class="reward_body">
        <img src="../assets/reward/mainbg.png" alt="" class="reward_bg">
        <img src="../assets/reward/title.png" alt="" class="title">
        <div class="reward_list">
            <img src="../assets/reward/reach.png" alt="" class="th1" v-if="peopleNums>49999">
            <img src="../assets/reward/reach.png" alt="" class="th2" v-if="peopleNums>99999">
            <img src="../assets/reward/reach.png" alt="" class="th3" v-if="peopleNums>199999">
            <img src="../assets/reward/reach.png" alt="" class="th4" v-if="peopleNums>299999">
            <img src="../assets/reward/reach.png" alt="" class="th5" v-if="peopleNums>499999">
        </div>
        <img src="../assets/reward/appointmentbtn.png" alt="" class="appointment_btn" @click="closePop(true)">
        <img src="../assets/reward/giftext.png" alt="" class="gift_text">
    </div>
    <Appointment :showpop='appoint' @closePop='closePop'></Appointment>
</template>
<script>
import Appointment from './Appointment'
export default {
    components:{Appointment},
    data:function(){
        return{
            peopleNums:0,
            appoint:false
        }
    },
    mounted(){
        this.getPeopleCount()
    },
    methods: {
        getPeopleCount:function(){
            this.getHttp(this,'','api/appointment/getPeopleCount',function(obj,res){
                if(res.code==200){
                    obj.peopleNums = res.data
                }else{
                    alert(res.message)
                }
            })
        },
        closePop:function(e){
            this.appoint = e
        }
    }
}
</script>
<style lang="less" scope="this api replaced by slot-scope in 2.5.0+">
    .reward_body{
        width: 100%;
        position: relative;
        img{
            display: block;
        }
        .title{
            position: absolute;
            width: 10rem;
            top: 2rem;
            left: .466667rem;
        }
        .reward_list{
            width: 9.88rem;
            height: 9.226667rem;
            position: absolute;
            left: 50%;
            margin-left: -5.1rem;
            top: 4.533333rem;
            background: url('../assets/reward/rewardimg.png') no-repeat;
            background-size: 100% 100%;
            .th1,.th2,.th3,.th4,.th5{
                position: absolute;
                width: 1.053333rem;
                left: 50%;
                margin-left: -.8rem;
                top: 0rem;
            }
            .th2{
                top:1.9rem
            }
            .th3{
                top: 3.7rem;
            }
            .th4{
                top: 5.6rem;
            }
            .th5{
                top: 7.5rem;
            }
        }
        .appointment_btn{
            position: absolute;
            width: 3.92rem;
            top: 50%;
            margin-top: 5.8rem;
            left: 50%;
            margin-left: -1.96rem;
        }
        .gift_text{
            position: absolute;
            width: 4.16rem;
            top: 50%;
            margin-top: 7.2rem;
            left: 50%;
            margin-left: -2.08rem;
        }
    }
</style>