import { createApp } from 'vue'
import App from './App.vue'
import 'animate.css'
import './assets/css/index.css'
import 'amfe-flexible'
import axios from 'axios'

const app = createApp(App)
app.config.globalProperties.$axios = axios
app.config.globalProperties.$regexp = {
    regNine:function(value){
     if(/9\d{8}/g.test(value) == false)
     return false
     else
     return true
   },
   regEight:function(value){
    if(/\d{8}/g.test(value) == false)
    return false
    else
    return true
   },
   regEleven:function(value){
    if(/\d{11}/g.test(value) == true||/\d{10}/g.test(value) == true)
    return true
    else
    return false
   },
}
/* 
 * 封装ajax
 * obj : 全局this
 * data : ajax传入后台data数据
 * address : ajax接口地址
 * fn : 成功返回方法  带参数  obj,data  obj : this data : response
 * */
var baseUrl = "https://admin.csback.gamemorefun.net/"
app.config.globalProperties.postHttp = function (obj, data, address, fn) {

  obj.$axios.post(baseUrl + address, data).then(response => {
    fn(obj, response.data);

  })
}
app.config.globalProperties.getHttp = function (obj, data, address, fn) {
  obj.$axios.get(baseUrl + address, { params: data }).then(function (response) {
    fn(obj, response.data)
  }).catch(function (response) {
    console.log(response);//发生错误时执行的代码
  });
}




app.mount('#app')

