<template>
  <div class="app_body" v-show="showpop">
    <div class="app_main animate__animated animate__fadeInDown">
      <div class="area_choice">
        <div class="area_select fl" @click="areaShowFn">
          {{ areaChoice }}
        </div>
        <div class="area_input fl">
          <input
            type="text"
            v-model="my_tel"
            :placeholder="my_pla"
            :maxlength="index_length"
            oninput="value=value.replace(/[^\d]/g,'')"
          />
        </div>
        <div class="area_list" :class="areaShow ? 'area_list_on' : ''">
          <div
            class="area_item fl"
            v-for="(item, index) in areaList"
            :key="index"
            @click="areaChoiceFn(item)"
          >
            {{ item.name }}
          </div>
        </div>
        <div class="rule" @click="checkRule">
          <img
            src="../assets/popup/checkin.png"
            alt=""
            class="check_img"
            v-if="rule"
          />
          <img
            src="../assets/popup/checkout.png"
            alt=""
            class="check_img"
            v-else
          />
          <img src="../assets/popup/text.png" alt="" class="text_img" />
        </div>
      </div>
      <div class="appoinment_gift">
        <img src="../assets/popup/appointgift.png" alt="" class="gift_img" />
        <img
          src="../assets/popup/appointmentbtn.png"
          alt=""
          class="appoint_btn"
          @click="appointmentFn"
        />
      </div>
      <div class="facebook_follow">
        <img src="../assets/popup/fbflower.png" alt="" class="follow_img" />
        <div class="belike">
          <div class="fb-like" data-href="https://www.facebook.com/ChunChiuM" data-width="" data-layout="button_count" data-action="like" data-size="large" data-share="false"></div>
        </div>
      </div>
      <div class="download_dom">
        <img
          src="../assets/popup/downloadbtn.png"
          alt=""
          class="download_img"
        />
        <div class="app_">
          <a href=""><div class="app_dom"></div></a>
          <a href=""><div class="app_dom"></div></a>
          <a href="https://www.facebook.com/ChunChiuM" target="_blank"><div class="app_dom"></div></a>
        </div>
      </div>
    </div>
    <img src="../assets/popup/x.png" alt="" class="app_close" @click="closePop">
  </div>
</template>
<script>
import { useToast } from "../Plugins/Toast";
export default {
  props: {
    showpop: {
      type: Boolean,
      require: true,
    },
  },
  setup() {
    const toast = useToast();
    return {
      toast,
    };
  },
  data() {
    return {
      areaList: [
        {
          id: 1,
          name: "台灣",
          tel: "+886",
          val: "tw",
          pla: "請填寫0後的9位數電話號碼",
          numlength: 9,
        },
        {
          id: 2,
          name: "香港",
          tel: "+852",
          val: "xg",
          pla: "請填寫8位數電話號碼",
          numlength: 8,
        },
        {
          id: 3,
          name: "澳門",
          tel: "+853",
          val: "am",
          pla: "請填寫8位數電話號碼",
          numlength: 8,
        },
        {
          id: 4,
          name: "新加坡",
          tel: "+65",
          val: "xjp",
          pla: "請填寫8位數電話號碼",
          numlength: 8,
        },
        {
          id: 5,
          name: "馬來西亞",
          tel: "+60",
          val: "mlxy",
          pla: "請填寫10或11位數電話號碼",
          numlength: 11,
        },
      ],
      areaShow: false,
      areaChoice: "台灣",
      rule: false, //是否勾选规定
      my_pla: "請填寫0後的9位數電話號碼",
      index_length: 9,
      my_tel: "",
      area_val: "tw",
      preventStatus: true, //是否可点击
    };
  },
  mounted(){
    
  },
  methods: {
    areaShowFn: function () {
      this.areaShow = !this.areaShow;
    },
    checkRule: function () {
      this.rule = !this.rule;
    },
    areaChoiceFn: function (e) {
      this.areaShow = false;
      this.my_tel = "";
      this.area_val = e.val; //選擇區域
      this.areaChoice = e.name; //區域名賦值
      this.my_pla = e.pla;
      this.index_length = e.numlength;
    },
    appointmentFn: function () {
      let tel;
      if (this.preventStatus) {
        this.preventStatus = false; //禁止连续点击
        if(!this.rule){
          this.toast('請您先勾選同意個人資料的蒐集使用及接收獎勵簡訊!')
          this.preventStatus = true
          return
        }
        switch (this.area_val){
          case 'tw':
            if(this.$regexp.regNine(this.my_tel)){
              tel = '886'+ this.my_tel
            }else{
              this.toast('號碼有誤請重新輸入');
              this.preventStatus = true
              return
            }
            break;
            case 'xg':
            if(this.$regexp.regEight(this.my_tel)){
              tel = '852'+ this.my_tel
            }else{
              this.toast('號碼有誤請重新輸入');
              this.preventStatus = true
              return
            }
            break;
            case 'am':
            if(this.$regexp.regEight(this.my_tel)){
              tel = '853'+ this.my_tel
            }else{
              this.toast('號碼有誤請重新輸入');
              this.preventStatus = true
              return
            }
            break;
            case 'xjp':
            if(this.$regexp.regEight(this.my_tel)){
              tel = '65'+ this.my_tel
            }else{
              this.toast('號碼有誤請重新輸入');
              this.preventStatus = true
              return
            }
            break;
            case 'mlxy':
            if(this.$regexp.regEleven(this.my_tel)){
              tel = '60'+ this.my_tel
            }else{
              this.toast('號碼有誤請重新輸入');
              this.preventStatus = true
              return
            }
            break;
        }
        let data = {
          phone:this.my_tel,
          zone:this.area_val
        }
        this.postHttp(this,data,'api/appointment/store',function(obj,res){
          obj.toast(res.message)
          obj.preventStatus = true
        })
      } else {
        return;
      }
    },
    closePop:function(){
      this.$emit('closePop',false)
    }
  },
};
</script>
<style lang="less" scope="this api replaced by slot-scope in 2.5.0+">
.app_body {
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
  left: 0;
  top: 0;
  z-index: 3;
  .app_main {
    position: absolute;
    width: 9.426667rem;
    height: 12.866667rem;
    top: 50%;
    margin-top: -6.8rem;
    left: 50%;
    margin-left: -4.71rem;
    background: url("../assets/popup/popbg.png") no-repeat;
    background-size: 100% 100%;
    padding: 0.4rem 0.3rem 0.4rem 0.4rem;
    box-sizing: border-box;
    .area_choice {
      width: 100%;
      height: 2rem;
      padding: 0.586667rem 0.2rem;
      box-sizing: border-box;
      position: relative;
      z-index: 2;
      .area_select {
        width: 2.4rem;
        height: 0.693333rem;
        background: url("../assets/popup/areaSelect.png") no-repeat;
        background-size: 100% 100%;
        color: white;
        font-size: 0.4rem;
        font-weight: 600;
        font-family: "SimSun";
        line-height: 0.69rem;
        padding: 0 0.55rem 0 0.1rem;
        position: relative;
        box-sizing: border-box;
        text-align: center;
      }
      .area_input {
        width: 5.52rem;
        margin-left: 0.3rem;
        height: 0.733333rem;
        input {
          width: 100%;
          height: 100%;
          box-sizing: border-box;
          color: #372f2f;
          font-size: 0.4rem;
          border: 1px solid #372f2f;
          background-color: #c9c1a4;
          display: block;
          padding: 0 0.2rem;
          outline: none;
          font-family: "SimSun";
        }
      }
      .rule {
        position: absolute;
        top: 1.5rem;
        left: 2.9rem;
        width: 5.52rem;
        color: #372f2f;
        font-family: "SimSun";
        font-size: 0.2rem;
        .check_img {
          width: 0.28rem;
          margin-right: 0.1rem;
        }
        .text_img {
          width: 4.773333rem;
        }
      }
      .area_list {
        position: absolute;
        width: 2.4rem;
        max-height: 0;
        box-sizing: border-box;
        top: 1.28rem;
        left: 0.2rem;
        background: url("../assets/popup/arealist.png") no-repeat;
        background-size: 100% 100%;
        padding: 0 0.133333rem;
        overflow: hidden;
        transition: all 0.3s ease;
        .area_item {
          width: 100%;
          border-bottom: 1px solid rgba(255, 255, 255, 0.5);
          font-size: 0.36rem;
          line-height: 0.62rem;
          font-weight: 600;
          color: white;
          font-family: "SimSun";
          text-align: center;
        }
        .area_item:last-child {
          border-bottom: 0;
        }
      }
      .area_list_on {
        max-height: 3.24rem;
      }
    }
    .appoinment_gift {
      width: 100%;
      height: 4rem;
      box-sizing: border-box;
      position: relative;
      text-align: center;
      overflow: hidden;
      .gift_img {
        width: 9.133333rem;
        max-width: inherit;
        display: block;
      }
      .appoint_btn {
        width: 3.386667rem;
        margin-top: 0.2rem;
      }
    }
    .facebook_follow {
      width: 100%;
      height: 2rem;
      box-sizing: border-box;
      position: relative;
      .follow_img {
        width: 8.16rem;
      }
      .belike{
        padding: 0 1rem;
      }
    }
    .download_dom {
      width: 100%;
      box-sizing: border-box;
      position: relative;
      .download_img {
        width: 8.026667rem;
        display: block;
        margin: auto;
      }
      .app_ {
        position: absolute;
        width: 8rem;
        left: 50%;
        margin-left: -4rem;
        bottom: 0;
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        .app_dom {
          width: 2.6rem;
          height: 0.76rem;
        }
      }
    }
  }
  .app_close{
    position: absolute;
    width: .853333rem;
    top: 50%;
    margin-top: 6.5rem;
    left: 50%;
    margin-left: -.424rem;
  }
}
</style>